<!-- 컴포넌트 UI 정의 -->
<template>
  <v-card-text class="black--text font-weight-bord" style="background: #f8f3f0">
    <v-row class="font">
      <v-col> 상품금액 </v-col>
      <v-col class="text-right"> {{ pprice == null ? 0 : String(pprice).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}원 </v-col>
    </v-row>
    <v-row class="font">
      <v-col> 배송비 </v-col>
      <v-col class="text-right"> {{ postp == null ? 0 : String(postp).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}원 </v-col>
    </v-row>
    <v-row class="mb-3">
      <v-col class="font"> 할인금액 </v-col>
      <v-col class="text-right textcolor"> -{{ discount == null ? 0 : String(discount).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}원 </v-col>
    </v-row>

    <v-divider class="black" />
    <v-row class="mt-3 just">
      <v-col v-if="content"> {{ content }} </v-col>
      <v-col v-else> 총{{ pamount == null ? 0 : pamount }}개 </v-col>
      <v-col class="text-right" style="font-size: 22px">
        {{
          String((pprice == null ? 0 : pprice) - (postp == null ? 0 : postp) - (discount == null ? 0 : discount)).replace(
            /\B(?=(\d{3})+(?!\d))/g,
            ",",
          )
        }}원
      </v-col>
    </v-row>
  </v-card-text>
</template>

<script>
export default {
  //컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "Payment",
  //추가하고 싶은 컴포넌트를 등록
  components: {},
  //컴포넌트에서 사용하는 데이터를 정의
  data() {
    return {
      totoalprice: 0,
    };
  },
  //컴포넌트에서 사용하는 메서드
  methods: {},

  props: ["pprice", "pamount", "discount", "postp", "content"],
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.just {
  color: black;
  font-weight: bold;
}
.font {
  font-size: 15px;
  color: black;
}
.textcolor {
  font-size: 15px;
  color: #c2935f;
}
</style>
