<template>
  <div class="text-center ma-2">
    <v-snackbar v-model="snackbar">
      {{ content }}
      <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="$emit('close')"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  name: "SnackBar",
  props: ["content", "snackbar"],
};
</script>

