<!-- 컴포넌트 UI 정의 -->
<template>
  <div>
    <div class="white black-txt pt-5" v-if="needHead">
      <v-row no-gutters class="orderfont">
        <v-col class="ml-5"> {{ new Date(date).toLocaleDateString().substring(0, 12) }} </v-col>
        <v-spacer></v-spacer>
        <v-col>
          <v-btn plain block class="orderfont" height="18" :to="url" v-if="url"> 상세정보 > </v-btn>
        </v-col>
      </v-row>
    </div>
    <v-card-text>
      <v-row style="background: #f5f5f5">
        <v-carousel
          :show-arrows="true"
          :show-arrows-on-hover="true"
          height="17%"
          :hide-delimiter-background="true"
          :hide-delimiters="true"
          class="pa-4"
          delimiter-icon="mdi-minus"
        >
          <v-carousel-item v-for="(product, i) in orderList" :key="product.cartno">
            <v-row align="center" justify="center" class="pb-2">
              <v-col class="body-2">
                <slot name="header">
                  <div class="font-weight-bold mb-1">
                    <span v-if="title">{{ title }}</span
                    ><span v-else>주문한 상품</span>{{ i + 1 }}
                  </div>
                </slot>
                <div class="font-weight-bold">
                  {{ product.pbrand }}
                </div>
                <div class="font-weight-regular mb-2">
                  {{ product.pname }}
                </div>
                <div class="font-weight-bold mb-1">{{ String(product.pprice).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}원</div>
                <div class="caption grey--text">
                  <span class="pr-4">사이즈 : {{ product.cartsize }}</span
                  ><span>색상 : {{ product.cartcolor }}</span>
                  <span class="ml-3">수량 : {{ product.cartamount }}</span>
                </div>
              </v-col>
              <v-col class="fill-height" cols="4">
                <v-img :src="product.imgurl1" width="100px"></v-img>
              </v-col>
            </v-row>
            <slot name="foot"> </slot>
          </v-carousel-item>
        </v-carousel>
      </v-row>
    </v-card-text>
  </div>
</template>

<script>
export default {
  //컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "Product",
  //추가하고 싶은 컴포넌트를 등록
  components: {},
  //컴포넌트에서 사용하는 데이터를 정의
  data() {
    return {};
  },
  //컴포넌트에서 사용하는 메서드
  methods: {},

  props: ["orderList", "title", "needHead", "date", "url"],
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
  text-shadow: 2px 3px 2px gray;
}
.just {
  color: black;
  font-weight: bold;
  font-size: 18px;
}
.orderfont {
  color: black;
  font-size: 12px;
}
</style>
