<!-- 컴포넌트 UI 정의 -->
<template>
  <v-card>
    <v-card-title class="active-tab text-h4">주문서</v-card-title>

    <v-card-text class="white black-txt pt-5">
      <div class="just">
        <span>주문상품</span>
      </div>
    </v-card-text>
    <!-- 상품 조회-->
    <Product :orderList="orderList" />

    <v-divider />
    <v-card-text class="black--text">
      <div>[배송입니다] 무료배송 30000만원</div>
    </v-card-text>
    <v-divider />
    <v-card-text class="white black-txt pt-5">
      <div class="just mb-3">주문자 정보</div>
      <v-divider class="black" />
      <div class="orderfont mt-3 ml-2">{{ member.name }} / {{ member.phone }}</div>
      <div class="orderfont mt-2 ml-2">{{ member.email }}</div>
    </v-card-text>

    <v-card-text class="white black-txt pt-5">
      <v-row no-gutters>
        <v-col class="just"> 배송지 정보 </v-col>
        <v-col class="text-right" style="font-size: 12px">
          <Add @close="dialogClose" />
        </v-col>
      </v-row>
      <v-divider class="black" />
      <div class="orderfont mt-3 ml-2">{{ addrs.toname }} / {{ addrs.tophone }}</div>
      <div class="orderfont mt-2 ml-2">[{{ addrs.addr1 }}] {{ addrs.addr2 }} {{ addrs.addr3 }}</div>
      <div class="orderfont mt-3 ml-2">
        <v-select class="form-control" :items="deliverygo" v-model="req"> </v-select>
      </div>
    </v-card-text>

    <!-- 결제 수단 -->
    <div>
      <v-card class="mx-auto mb-5" max-width="344">
        <div class="d-flex justify-space-between">
          <h4 class="pa-0 pl-3 pt-3 pb-3">결제 수단</h4>
          <v-icon icon @click="cardbank = !cardbank">
            {{ cardbank ? "mdi-chevron-up" : "mdi-chevron-down" }}
          </v-icon>
        </div>
        <v-expand-transition>
          <div v-show="cardbank">
            <div class="d-flex justify-space-around">
              <v-btn class="ma-0 font-weight-bold" width="45%" height="40" @click="chk = false" :color="chk ? '' : 'black white--text'">카드 </v-btn>
              <v-btn class="ma-0 font-weight-bold" width="45%" height="40" @click="chk = true" :color="chk ? 'black white--text' : ''">
                무통장
              </v-btn>
            </div>
            <v-col class="d-fl ex" cols="12" sm="6">
              <v-select :items="cards" label="선택" dense v-model="payment.pcompany" color="black"></v-select>
            </v-col>
          </div>
        </v-expand-transition>
      </v-card>
    </div>
    <v-divider />
    <v-card-text style="background: #f8f3f0">
      <v-container>
        <Payment :pamount="selectamount" :pprice="selectprice" :content="'최종결제금액'"> </Payment>
      </v-container>
    </v-card-text>
    <v-divider class="grey lighten-5 mt-2" />
    <order-footer :just="just">
      <template v-slot:click>
        <v-btn block style="background: #c2935f" @click="goUrl()">
          <v-icon class="button" color="white">결제 하기</v-icon>
        </v-btn>
      </template>
    </order-footer>
    <Snack-bar :content="scontent" :snackbar="snackbar" @close="snackbar = false" />
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-card>
</template>

<script>
import Product from "@/components/order/Product.vue";
import Payment from "@/components/order/Payment.vue";
import Add from "@/components/tool/Add.vue";
import orderProductApi from "@/apis/cart";
import MemberApi from "@/apis/member";
import orderInfoApi from "@/apis/order";
import OrderFooter from "@/components/footer/OrderFooter.vue";
import SnackBar from "@/components/tool/SnackBar.vue";
export default {
  //컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "",
  //추가하고 싶은 컴포넌트를 등록
  components: { Product, Add, Payment, OrderFooter, SnackBar },
  //컴포넌트에서 사용하는 데이터를 정의
  data() {
    return {
      snackbar: false,
      chk: false,
      just: true,
      req: "",
      member: {},
      deliverygo: ["배송기사에게 전달되는 메시지입니다.", "배송전 연락 바랍니다.", "부재시, 전화주시거나 또는 문자 주세요."],
      flag: false,
      cartnoList: [],
      orderList: [],
      orderProductList: [],
      ordersheet: {},
      scrollPosition: 0,
      selectamount: 0,
      selectprice: 0,
      beforePosition: 0,
      cardbank: true,
      radios: true,
      cards: ["신한", "하나", "현대", "국민"],
      banks: ["신한", "하나", "현대", "국민"],
      addrs: {},
      payment: {
        pcategory: "",
        pcompany: "",
      },
      scontent: "",
      overlay: false,
    };
  },
  //컴포넌트에서 사용하는 메서드
  methods: {
    goUrl() {
      if (this.payment.pcompany.trim().length < 1) {
        this.scontent = "결제 수단을 선택해주세요.";
        this.snackbar = true;
        return;
      }

      this.ordersheet["mno"] = this.$store.getters.getMno;
      this.ordersheet["oaddr1"] = this.addrs.addr1;
      this.ordersheet["oaddr2"] = this.addrs.addr2;
      this.ordersheet["oaddr3"] = this.addrs.addr3;
      this.ordersheet["oname"] = this.addrs.toname;
      this.ordersheet["ophone"] = this.addrs.tophone;
      this.ordersheet["orequest"] = this.req;

      let totalPrice = 0;
      this.orderProductList = [];
      for (let o in this.orderList) {
        let obj = {};
        obj["pcode"] = this.orderList[o].pcode;
        obj["pname"] = this.orderList[o].pname;
        obj["psize"] = this.orderList[o].cartsize;
        obj["pcolor"] = this.orderList[o].cartcolor;
        obj["opamount"] = this.orderList[o].cartamount;
        obj["pbrand"] = this.orderList[o].pbrand;
        obj["pprice"] = this.orderList[o].pprice;
        totalPrice += this.orderList[o].cartamount * this.orderList[o].pprice;
        this.orderProductList.push(obj);
      }
      this.ordersheet["ototalprice"] = totalPrice;
      this.ordersheet["ostackmileage"] = Math.ceil(totalPrice * 0.02);

      if (this.chk) {
        this.payment.pcategory = "bank";
      } else {
        this.payment.pcategory = "card";
      }
      this.overlay = true;
      orderInfoApi
        .createOrder(this.ordersheet, this.orderProductList, this.payment, this.cartnoList)
        .then((res) => {
          console.log(res);
          if (res.data > 0) {
            this.$router.replace("/order/orderComplete?ono=" + res.data);
          } else {
            this.scontent = "죄송합니다. 품절된 상품이 포함되어 있습니다.";
            this.snackbar = true;
          }
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          this.overlay = false;
        });
    },
    dialogClose() {
      console.log(this.addr);
    },
    async orderProduct(cartno) {
      try {
        const promise = await orderProductApi.getorderProduct(cartno);
        this.orderList = promise.data.orderProduct;
      } catch (error) {
        console.log(error);
      }
    },
    async getMemberInfo() {
      try {
        let promise = await MemberApi.getMember(this.$store.getters.getMno);
        this.member = promise.data;
      } catch (error) {
        console.log(error);
      }
    },
    async getOredrInfo(ono) {
      try {
        let promise = await orderInfoApi.getorderPepole(ono);
        this.ordersheet = promise.data.ordersheet;
      } catch (error) {
        console.log(error);
      }
    },
    handleScroll(e) {
      this.scrollPosition = e.target.documentElement.scrollTop;
      if (this.scrollPosition == 0 || this.beforePosition <= this.scrollPosition) {
        this.just = true;
      } else {
        this.just = false;
      }
      this.beforePosition = this.scrollPosition;
    },
    async addrList(mno) {
      try {
        let promise = await MemberApi.getAddress(mno);
        console.log(promise);
        this.addrs = promise.data[0];
      } catch (error) {
        console.log(error);
      }
    },
  },

  created() {
    this.addrList(this.$store.getters.getMno);
    this.cartnoList = this.$route.query.cartno;
    this.selectamount = this.$route.query.selectamount;
    this.selectprice = this.$route.query.selectprice;

    this.getMemberInfo();
    //this.getOredrInfo();
    for (let i = 0; i < this.cartnoList.length; i++) {
      orderProductApi.getorderProduct(this.cartnoList[i]).then((res) => {
        this.orderList.push(res.data);
      });
    }
  },
  beforeMount() {
    this.orderList.sort((a, b) => {
      return b.cartno - a.cartno;
    });
  },
  mounted() {
    window.scrollTo(0, 0);
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
  text-shadow: 1px 1px 1px gray;
}
.just {
  color: black;
  font-weight: bold;
  font-size: 18px;
}
.orderfont {
  color: black;
  font-size: 13px;
}
.font {
  color: black;
  font-size: 14px;
  font-weight: bold;
}
.button {
  color: white;
  font-weight: bold;
  font-size: 20px;
}
</style>
