<!-- 컴포넌트 UI 정의 -->
<template>
  <v-dialog persistent max-width="600px" v-model="flag">
    <template v-slot:activator="{ on, attrs }">
      <v-btn :input-value="on" plain block class="brown lighten-4 black--text font-weight-bold" v-bind="attrs" v-on="on"> 옵션변경 </v-btn>
    </template>
    <v-card>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12" sm="6" md="4">
              <div class="just">{{ pbrand }}</div>
              <div class="just">{{ pname }}</div>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <!-- 컬러 변경 -->
              <v-item-group mandatory v-model="selectColor">
                <v-container>
                  <v-row no-gutters>
                    <v-item v-for="color in coloroption" :key="color.pcolor" :value="color.pcolor" v-slot="{ active, toggle }">
                      <v-card
                        color="white"
                        class="mr-3 rounded-circle pa-0"
                        dark
                        @click="toggle"
                        v-on:click="changeColor(color.pcolor)"
                        width="30"
                        height="30"
                      >
                        <v-img :src="color.colorurl" class="d-flex text-center align-center rounded-circle"
                          ><v-icon>
                            {{ active ? "mdi-check" : "" }}
                          </v-icon></v-img
                        >
                        {{ color.pcolor == pcolor }}
                      </v-card>
                    </v-item>
                  </v-row>
                </v-container>
              </v-item-group>
              <!--컬러 변경 끝 -->
            </v-col>
            <v-col cols="12" sm="6">
              <!-- 사이즈 변경 -->
              <v-select v-model="selectpsize" :items="amountList" item-text="psize" label="사이즈"> </v-select>
            </v-col>
            <v-col cols="12" sm="6">
              <!-- 수량 변경 -->
              <v-card-actions v-model="pamount">
                <div>
                  <v-btn depressed color="white" @click="decreaseScore(player_one)">
                    <v-icon> mdi-minus </v-icon>
                  </v-btn>
                </div>
                <v-spacer></v-spacer>
                <h3>{{ player_one.score }}</h3>
                <v-spacer></v-spacer>
                <div>
                  <v-btn depressed color="white" @click="increaseScore(player_one)">
                    <v-icon> mdi-plus </v-icon>
                  </v-btn>
                </div>
              </v-card-actions>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close"> Close </v-btn>
        <v-btn color="blue darken-1" text @click="changeData"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import cartOptionApi from "@/apis/cart";
import sizeOptionApi from "@/apis/product";
export default {
  //컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "Bar",
  //추가하고 싶은 컴포넌트를 등록
  components: {},
  //컴포넌트에서 사용하는 데이터를 정의
  data() {
    return {
      player_one: {
        score: 0,
      },
      selectpsize: null,
      selectColor: null,
      flag: false,
      coloroption: [],
      imgIndex: 0,
      amountDict: {},
      amountList: {},
      items: [],
    };
  },
  //컴포넌트에서 사용하는 메서드
  methods: {
    // 수량 카운트
    increaseScore(player) {
      if (player.score < this.amountDict[this.psize]) player.score += 1;
    },
    decreaseScore(player) {
      if (player.score > 1) {
        player.score -= 1;
      }
    },
    close() {
      this.flag = false;
    },
    changeColor(val) {
      this.selectColor = val;
      this.imgIndex = 0;
      this.setAmountDict(this.pcode, val);
      this.player_one.score = 1;
    },
    async getcartOption(pcode) {
      try {
        const promise = await cartOptionApi.getcartoption(pcode);
        this.coloroption = promise.data.cartOption;
      } catch (error) {
        console.log(error);
      }
    },
    setAmountDict(pcode, pcolor) {
      sizeOptionApi.getProductSizeAmount(pcode, pcolor).then((response) => {
        let amountList = response.data.amount;
        amountList.forEach((amount) => {
          this.amountDict[amount.psize] = amount.pamount;
        });
        this.amountList = amountList;
        this.sizeSort(this.amountList);
        this.$emit("isSoldOut", this.cartno, this.amountDict[this.psize], this.pamount);
      });
    },
    sizeSort(arr) {
      var sizeDict = {
        XSS: -1,
        XXS: 0,
        XS: 1,
        S: 2,
        MS: 3,
        M: 4,
        ML: 5,
        L: 6,
        XL: 7,
        XXL: 8,
        XXXL: 9,
        FR: 100,
      };
      arr.sort(function (a, b) {
        let tempA = a.psize;
        let tempB = b.psize;
        if (isNaN(tempA)) {
          tempA = sizeDict[tempA];
        }
        if (isNaN(tempB)) {
          tempB = sizeDict[tempB];
        }
        if (tempA > tempB) {
          return 1;
        } else if (tempA < tempB) {
          return -1;
        } else {
          return 0;
        }
      });
    },
    async changeData() {
      if (this.pcolor != this.selectColor || this.pamount != this.player_one.score || this.psize != this.selectpsize) {
        let cartProduct = {};
        cartProduct["cartcolor"] = this.selectColor;
        cartProduct["cartsize"] = this.selectpsize;
        cartProduct["cartamount"] = this.player_one.score;
        cartProduct["cartno"] = this.cartno;
        const promise = await cartOptionApi.modifyOpction(cartProduct);
        if (promise.data.result == "success") {
          this.$emit("datachange");
        }
      }
      this.close();
    },
  },
  props: ["pbrand", "pcolor", "pamount", "pname", "psize", "pcode", "cartno"],
  created() {
    this.selectpsize = this.psize;
    this.selectColor = this.pcolor;
    this.player_one.score = this.pamount;
    this.getcartOption(this.pcode);
    this.setAmountDict(this.pcode, this.pcolor);
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.just {
  color: black;
  font-weight: bold;
}
</style>
