<!-- 컴포넌트 UI 정의 -->
<template>
  <v-card>
    <v-card-title class="active-tab text-h4">주문완료</v-card-title>
    <v-divider class="grey lighten-3" />
    <v-card-text> </v-card-text>

    <Product :orderList="orderProductDetailList">
      <template v-slot:header>
        <div class="font-weight-bold mb-1">주문 완료</div>
      </template>
    </Product>

    <v-divider />
    <v-card-text class="pt-6">
      <div class="just mb-3">결제 수단</div>
      <v-divider class="black" />
      <div class="orderfont mt-3 ml-2" v-if="nowpayment == 'bank'">무통장결제</div>
      <div class="orderfont mt-3 ml-2" v-else>카드 결제</div>
      <div class="orderfont mt-3 ml-2 pb-3" v-if="nowpayment == 'bank'">은행명: {{ nowpayment.pcompany }} / 계좌번호:12123123132</div>
      <div class="orderfont mt-3 ml-2 pb-3" v-else>카드사명: {{ nowpayment.pcompany }} / 계좌번호:12123123132</div>
    </v-card-text>

    <v-divider />
    <v-card-text style="background: #f8f3f0">
      <v-container class="orderfont">
        <p class="text-h6" style="font-weight: bold">최종결제금액</p>
        <Payment :pamount="selectamount" :pprice="selectprice" :content="'최종결제금액'"> </Payment>
      </v-container>
    </v-card-text>
    <v-divider />
    <v-card-text>
      <div class="just mb-3 mt-4">배송지 정보</div>
      <v-divider class="black" />
      <div class="orderfont mt-3 ml-2">{{ addrs.toname }} / {{ addrs.tophone }}</div>
      <div class="orderfont mt-2 ml-2">[{{ addrs.addr1 }}] {{ addrs.addr2 }} {{ addrs.addr3 }}</div>
    </v-card-text>
    <v-divider />
    <v-card-text>
      <div class="just mt-10" style="font-weight: bold">함께 구매하면 좋은 상품</div>
    </v-card-text>
    <!-- 함께 구매하면 좋은 상품 -->
    <v-container>
      <v-carousel cycle hide-delimiters hide-delimiter-background show-arrows-on-hover>
        <v-carousel-item v-for="(togeter, i) in togeter" :key="i">
          <v-sheet class="mx-auto" max-width="400" out>
            <v-slide-group v-model="model" class="pa-2" active-class="success">
              <v-slide-item v-for="n in 15" :key="n" v-slot="{ toggle }">
                <v-card class="ma-1" height="200" width="150" @click="toggle" tile>
                  <v-img class="ma-1" width="150" height="200" :src="require(`@/assets/photos/${togeter.img}`)" />
                </v-card>
              </v-slide-item>
            </v-slide-group>
          </v-sheet>
          <v-card-text>
            <div class="black--text grey lighten-3">
              <div class="mb-2">[{{ togeter.brand }}]</div>
              <div class="mb-2">{{ togeter.productname }}</div>
              <div>{{ togeter.price }}원</div>
            </div>
          </v-card-text>
        </v-carousel-item>
      </v-carousel>
    </v-container>
    <!-- 함께 구매하면 좋은 상품 끝 -->
    <v-bottom-navigation grow app v-if="just" class="ma-0 pa-0 fill-height">
      <v-row class="text-center ma-0 pa-0" align-content="center" no-gutters>
        <v-col cols="6" class="fill-height"
          ><v-btn style="background: #333333" block @click="goUrl()">
            <v-icon class="button" color="white">쇼핑계속하기</v-icon>
          </v-btn>
        </v-col>
        <v-col cols="6" class="fill-height">
          <v-btn style="background: #c2935f" block @click="goshop()">
            <v-icon class="button" color="white">주문내역조회</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-bottom-navigation>
  </v-card>
</template>

<script>
import Product from "@/components/order/Product.vue";
import Payment from "@/components/order/Payment.vue";
import orderInfoApi from "@/apis/order";
import MemberApi from "@/apis/member";
import ProductApi from "@/apis/product";
export default {
  //컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "OrderComplete",
  //추가하고 싶은 컴포넌트를 등록
  components: { Product, Payment },
  //컴포넌트에서 사용하는 데이터를 정의
  data() {
    return {
      model: "",
      just: true,
      scrollPosition: 0,
      beforePosition: 0,
      orderInfo: {},
      cartnoList: [],
      selectamount: 0,
      selectprice: 0,
      addrs: {},
      orderSheet: {},
      orderProductDetailList: [],
      nowpayment: {},
      togeter: [
        {
          img: "photo1.jpg",
          brand: "rnenqk",
          productname: "네이비 세일러...",
          price: "20000",
        },
        {
          img: "photo2.jpg",
          brand: "hihihihihi",
          productname: "남성 브랜딩 셔츠...",
          price: "52000",
        },
        {
          img: "photo4.jpg",
          brand: "rnenqk",
          productname: "가나다라마바사...",
          price: "30000",
        },
      ],
    };
  },
  //컴포넌트에서 사용하는 메서드
  methods: {
    goUrl() {
      this.$router.push("/");
    },
    goshop() {
      this.$router.push("/order/orderList");
    },
    handleScroll(e) {
      this.scrollPosition = e.target.documentElement.scrollTop;
      if (this.scrollPosition == 0 || this.beforePosition <= this.scrollPosition) {
        this.just = true;
      } else {
        this.just = false;
      }
      this.beforePosition = this.scrollPosition;
    },
    async addrList(mno) {
      try {
        let promise = await MemberApi.getAddress(mno);
        this.addrs = promise.data[0];
      } catch (error) {
        console.log(error);
      }
    },
    async getOrderProductList() {
      try {
        const promise = await orderInfoApi.getOrderSheet(this.$store.getters.getMno, this.$route.query.ono);
        this.orderSheet = promise.data.ordersheet[0];
        this.selectprice = 0;

        for (let key of promise.data.orderproductlist) {
          const productdetail = await ProductApi.getProductDetail(key.pcode);
          for (let colorkey in productdetail.data.color) {
            if (productdetail.data.color[colorkey].pcolor == key.pcolor) {
              let obj = {
                pbrand: productdetail.data.pbrand,
                pname: key.pname,
                cartsize: key.psize,
                cartcolor: key.pcolor,
                cartamount: key.opamount,
                imgurl1: productdetail.data.color[colorkey].imgurl1,
                cartno: key.opno,
                pprice: productdetail.data.pprice * key.opamount,
              };
              this.selectprice += Number.parseInt(obj.pprice);
              this.orderProductDetailList.push(obj);
              this.selectamount += 1;
              break;
            }
          }
        }

        const payment = await orderInfoApi.getPayment(this.$route.query.ono);
        this.nowpayment = payment.data;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.addrList(this.$store.getters.getMno);
    this.getOrderProductList();
    this.cartnoList = this.$route.query.cartno;
    this.selectamount = this.$route.query.selectamount;
    this.selectprice = this.$route.query.selectprice;
    console.log("data : ", this.orderProductDetailList);
  },
  mounted() {
    window.scrollTo(0, 0);
  },

  props: [""],
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
  text-shadow: 1px 1px 1px gray;
}
.just {
  color: black;
  font-weight: bold;
  font-size: 14px;
}
.orderfont {
  color: black;
  font-size: 14px;
}
.togeterP {
  color: black;
  font-size: 20px;
}
.togetera {
  font-size: 18px;
  font-weight: bold;
}
.font {
  color: black;
  font-size: 18px;
  font-weight: bold;
}
.button {
  color: black;
  font-size: 18px;
  font-weight: bold;
}
</style>
