<!-- 컴포넌트 UI 정의 -->
<template>
  <v-card>
    <v-card-title class="active-tab text-h4">주문 상세</v-card-title>
    <Product :orderList="orderProductDetailList" :needHead="true" :date="orderSheet.oregdate"></Product>
    <div>
      <v-card-text class="white black-txt pt-5">
        <div class="just mb-3">주문정보</div>
        <v-divider class="black" />
        <v-row class="pt-2">
          <v-col><div class="orderfont mt-3 ml-2">주문자 정보</div></v-col>
          <v-col><div class="orderfont mt-2 ml-2">이창현 / 010-5754-2656 dlckdgus205@naver.com</div></v-col>
        </v-row>
        <v-row class="mt-1">
          <v-col><div class="orderfont mt-3 ml-2">받는분</div></v-col>
          <v-col
            ><div class="orderfont mt-2 ml-2">{{ orderSheet.oname }} / {{ orderSheet.ophone }}</div></v-col
          >
        </v-row>
        <v-row class="mb-2">
          <v-col><div class="orderfont mt-3 ml-2">배송지 주소</div></v-col>
          <v-col
            ><div class="orderfont mt-2 ml-2">[{{ orderSheet.oaddr1 }}] {{ orderSheet.oaddr2 }} {{ orderSheet.oaddr3 }}</div></v-col
          >
        </v-row>
        <v-row justify="center" class="pa-0 ma-0 brown lighten-4 black--text font-weight-bold">
          <v-btn plain block class="font-weight-bold">주문 취소</v-btn>
        </v-row>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import Product from "@/components/order/Product.vue";
import OrderApi from "@/apis/order";
import ProductApi from "@/apis/product";
export default {
  //컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "ProductDetails",
  //추가하고 싶은 컴포넌트를 등록
  components: {
    Product,
  },
  //컴포넌트에서 사용하는 데이터를 정의
  data() {
    return {
      orderSheet: {},
      orderProductDetailList: [],
    };
  },
  //컴포넌트에서 사용하는 메서드
  methods: {
    async getOrderProductList() {
      try {
        const promise = await OrderApi.getOrderSheet(this.$store.getters.getMno, this.$route.query.ono);
        this.orderSheet = promise.data.ordersheet[0];
        for (let key of promise.data.orderproductlist) {
          const productdetail = await ProductApi.getProductDetail(key.pcode);
          for (let colorkey in productdetail.data.color) {
            if (productdetail.data.color[colorkey].pcolor == key.pcolor) {
              let obj = {
                pbrand: productdetail.data.pbrand,
                pname: key.pname,
                cartsize: key.psize,
                cartcolor: key.pcolor,
                cartamount: key.opamount,
                imgurl1: productdetail.data.color[colorkey].imgurl1,
                cartno: key.opno,
                pprice: productdetail.data.pprice * key.opamount,
              };

              this.orderProductDetailList.push(obj);
              break;
            }
          }
        }
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getOrderProductList();
  },
  mounted() {
    window.scrollTo(0, 0);
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
  text-shadow: 1px 1px 1px gray;
}
.gotab {
  background-color: rgb(238, 214, 178);
  font-weight: bold;
}
.orderfont {
  color: black;
  font-size: 13px;
}
.just {
  color: black;
  font-weight: bold;
  font-size: 18px;
}
</style>
