<!-- 컴포넌트 UI 정의 -->
<template>
  <v-dialog v-model="flag" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn :input-value="on" plain block class="mt-3 ml-5" height="15" v-bind="attrs" v-on="on"> + 다른 배송지 찾기 </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <span class="font-weight-bold text-h5">다른 배송지 찾기</span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row no-gutters>
            <v-col cols="12" sm="6" md="4">
              <v-text-field label="수신자명(필수)" required></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-text-field type="number" label="휴대폰번호(필수)" required></v-text-field>
            </v-col>
            <v-col cols="12" md="4">
              <v-text-field label="배송지 주소" v-model="address" required></v-text-field>
              <v-btn class="w-button3" @click="openDaumPostcode">주소 검색</v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <div ref="wrap" style="display: none; border: 1px solid; width: 320px; height: 300px; margin: 5px 0; position: relative">
        <v-img
          src="//t1.daumcdn.net/postcode/resource/images/close.png"
          style="cursor: pointer; position: absolute; right: 0px; top: -1px; z-index: 1"
          @click="foldDaumPostcode"
          alt="접기 버튼"
        />
      </div>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close"> 취소 </v-btn>
        <v-btn color="blue darken-1" text @click="$emit('close')"> 확인 </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  //컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "Add",
  //추가하고 싶은 컴포넌트를 등록
  components: {},
  //컴포넌트에서 사용하는 데이터를 정의
  data() {
    return {
      flag: false,
    };
  },
  //컴포넌트에서 사용하는 메서드
  methods: {
    close() {
      this.flag = false;
    },
    openDaumPostcode() {
      const elementWrap = this.$refs.wrap;
      // 현재 scroll 위치를 저장해놓는다.
      const currentScroll = Math.max(document.body.scrollTop, document.documentElement.scrollTop);
      new window.daum.Postcode({
        oncomplete: (data) => {
          // 검색결과 항목을 클릭했을때 실행할 코드를 작성하는 부분.

          // 각 주소의 노출 규칙에 따라 주소를 조합한다.
          // 내려오는 변수가 값이 없는 경우엔 공백('')값을 가지므로, 이를 참고하여 분기 한다.
          let addr = ""; // 주소 변수

          // 사용자가 선택한 주소 타입에 따라 해당 주소 값을 가져온다.
          if (data.userSelectedType === "R") {
            // 사용자가 도로명 주소를 선택했을 경우
            addr = data.roadAddress;
          } else {
            // 사용자가 지번 주소를 선택했을 경우(J)
            addr = data.jibunAddress;
          }

          // 우편번호와 주소 정보를 해당 필드에 넣는다.
          // document.getElementById('address').value = addr
          this.address = addr;

          // iframe을 넣은 element를 안보이게 한다.
          // (autoClose:false 기능을 이용한다면, 아래 코드를 제거해야 화면에서 사라지지 않는다.)
          elementWrap.style.display = "none";

          // 우편번호 찾기 화면이 보이기 이전으로 scroll 위치를 되돌린다.
          document.body.scrollTop = currentScroll;
        },
        // 우편번호 찾기 화면 크기가 조정되었을때 실행할 코드를 작성하는 부분. iframe을 넣은 element의 높이값을 조정한다.
        onresize: function (size) {
          elementWrap.style.height = size.height + "px";
        },
        width: "100%",
        height: "100%",
      }).embed(elementWrap);
      elementWrap.style.display = "block";
    },
    foldDaumPostcode() {
      const elementWrap = this.$refs.wrap;
      elementWrap.style.display = "none";
    },
  },
};
</script>

<!-- 컴포넌트 스타일 정의 -->
<style scoped></style>
